.video {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &__text {
    font-family: $text-font;
    text-align: justify;

    @include tablet {
      text-align: left;
      line-height: 25px;
    }
  }
  &__container {
    margin: 50px;
  }
}
